import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {ConfigProvider} from "../../providers/config/config";
import {FundTransferOtherBankProvider} from "../../providers/fund-transfer-other-bank/fund-transfer-other-bank";


@IonicPage()
@Component({
  selector: 'page-add-credit-account-other-bank02',
  templateUrl: 'add-credit-account-other-bank02.html',
})
export class AddCreditAccountOtherBank02Page {

  addCreditAccountOBError02 = "";
  subAccDetail;
  fundTransferType;
  receiverType;
  eftBankDetail;
  eftBranchDetail;
  npsbBankDetail;
  eftFlag;

  encRoutingNo;
  encRecAcc;
  encRecName;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public iTransferService: FundTransferOtherBankProvider,
              public alertCtrl: AlertController,
              public config: ConfigProvider) {

    this.subAccDetail = JSON.parse(this.navParams.get("subAccDetail"));
    this.eftBankDetail = this.navParams.get("eftBankDetail");
    this.eftBranchDetail = this.navParams.get("eftBranchDetail");
    this.npsbBankDetail = this.navParams.get("npsbBankDetail");

    if (typeof(this.eftBankDetail) != "undefined") {
      this.eftBankDetail = JSON.parse(this.eftBankDetail);
    }
    if (typeof(this.eftBranchDetail) != "undefined") {
      this.eftBranchDetail = JSON.parse(this.eftBranchDetail);
    }
    if (typeof(this.npsbBankDetail) != "undefined") {
      this.npsbBankDetail = JSON.parse(this.npsbBankDetail);
    }

    this.fundTransferType = this.navParams.get("fundTransferType");
    this.receiverType = this.navParams.get("receiverType");
    this.encRoutingNo = this.navParams.get("encRoutingNo");
    this.encRecAcc = this.navParams.get("encRecAcc");
    this.encRecName = this.navParams.get("encRecName");

    this.eftFlag = this.fundTransferType != this.config.transferTypeNPSB;


  }

  AddNewCreditAccountOB02Action = function () {

    this.addCreditAccountOBError02 = "";
    let flag = true;

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let oneTimeKeyVal="";
      let appData = this.storage.retrieveFromStorage("appData");
      let encTxService = this.cryptoUtil.getEncryptVal(this.fundTransferType);
      let encRecType = this.cryptoUtil.getEncryptVal(this.receiverType);
      let encOtp = this.cryptoUtil.getEncryptVal(oneTimeKeyVal);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.iTransferService.confirmAddObFtBeneficiaryAccount(unMaskToken, appData.userId, appData.appId, encTxService, encRecType, this.encRoutingNo, this.encRecAcc, this.encRecName, encOtp)
        .then(data => {
          this.onConfirmOBAddCreditAccComplete(data);
        });
    }
  };

  onConfirmOBAddCreditAccComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('AddCreditAccountOtherBank03Page',
        {
          'subAccDetail': JSON.stringify(data.object),
          'fundTransferType': this.fundTransferType,
          'receiverType': this.receiverType,
          'eftBankDetail': JSON.stringify(this.eftBankDetail),
          'eftBranchDetail': JSON.stringify(this.eftBranchDetail),
          'npsbBankDetail': JSON.stringify(this.npsbBankDetail),
        });
    }

    else if (data.statusCode == 0) {
      this.addCreditAccountOBError02 = data.note;
    }

    else {
      this.addCreditAccountOBError02 = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {

  }

}



